import './App.css'
// import { createBrowserRouter, RouterProvider, Outlet } from 'react-router-dom'
import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom'
import Home from './pages/home/Home'
import AboutUs from './pages/about-us/AboutUs'
import OurProjects from './pages/ourProjects/OurProjects'
import OurProducts from './pages/our-products/OurProducts'
import Header from './components/header/Header'
import Footer from './components/footer/Footer'
import Contact from './pages/contact/Contact'
import Pricelist from './pages/priceList/Pricelist'
import BestSellingGoods from './pages/bestSellingGoods/BestSellingGoods'
import LastProjects from './pages/lastProjects/LastProjects'
import Register from './pages/register/Register'
import Login from './pages/login/Login'
import { useEffect } from 'react'
import Pay from './pages/pay/Pay'
import Success from './pages/success/Success'
import Products from './pages/products/Products'
import Product from './pages/product/Product'
import { useSelector } from 'react-redux'

function App() {
    useEffect(() => {
        let ibg = document.querySelectorAll('.ibg')
        for (var i = 0; i < ibg.length; i++) {
            if (ibg[i].querySelector('img')) {
                ibg[i].style.backgroundImage =
                    'url(' + ibg[i].querySelector('img').getAttribute('src') + ')'
            }
        }
    })

    const user = useSelector((state) => state.user.currentUser)

    return (
        <div className='wrapper'>
            <main className='page'>
                <BrowserRouter>
                    <Header />
                    <Routes>
                        <Route path='/' index exact element={<Home />} />
                        <Route path='/login' element={user ? <Navigate to='/' /> : <Login />} />
                        <Route path='/about-us' element={<AboutUs />} />
                        <Route path='/our-projects' element={<OurProjects />} />
                        <Route path='/our-products' element={<OurProducts />} />
                        <Route path='/contact' element={<Contact />} />
                        <Route path='/price-list' element={<Pricelist />} />
                        <Route path='/best-selling-goods' element={<BestSellingGoods />} />
                        <Route path='/last-projects' element={<LastProjects />} />
                        <Route path='/pay' element={<Pay />} />
                        <Route path='/success' element={<Success />} />
                        <Route path='/products/:id' element={<Products />} />
                        <Route path='/product/:id' element={<Product />} />
                        <Route path='/register' element={<Register />} />
                    </Routes>
                    <Footer />
                </BrowserRouter>
            </main>
        </div>
    )
}

export default App

