import './list.scss'
import Card from './../card/Card'
import useFetch from '../../hooks/useFetch'

const List = ({ subCats, maxPrice, sort, catId }) => {
    const { data, loading, error } = useFetch( catId ? `/products?category=${catId}`: `/products`)
    return (
        <div className='list'>
            {error
                ? 'Something was wrong'
                : loading
                ? 'Loading'
                : data?.map((item) => {
                      return <Card product={item} key={item.id} />
                  })}
        </div>
    )
}

export default List
