import { Link } from 'react-router-dom' 
import './categories.scss'
import Protection from '../../assets/categories/electrical_protection_and_control.jpg'
import Emergency from '../../assets/categories/emergency-lighting.jpg'
import Network from '../../assets/categories/network.jpg'
import Materials from '../../assets/categories/materials-and-system.JPG'
import Enclosures from '../../assets/categories/enclosures.JPG'
import Sockets from '../../assets/categories/sockets.JPG'

const Categories = () => {
    return (
        <div className='categories'>
            <div className='col'>
                <div className='row'>
                    <Link to='/products/5' className='link'>
                        <button className='button'>Electrical protection and control</button>
                        <img src={Protection} alt='' />
                    </Link>
                </div>
                <div className='row'>
                    <Link to='/products/1' className='link'>
                        <button className='button'>Emergency lighting</button>
                        <img src={Emergency} alt='' />
                    </Link>
                </div>
            </div>
            <div className='col'>
                <div className='row'>
                    <Link to='/products/3' className='link'>
                        <button className='button'>Installation material and system</button>
                        <img src={Materials} alt='' />
                    </Link>
                </div>
            </div>
            <div className='col col-l'>
                <div className='row'>
                    <div className='col'>
                        <div className='row'>
                            <Link to='/products/4' className='link'>
                                <button className='button'>Light switches and electrical sockets</button>
                                <img src={Sockets} alt='' />
                            </Link>
                        </div>
                    </div>
                    <div className='col'>
                        <div className='row'>
                            <Link to='/products/6' className='link'>
                                <button className='button'>Network connectivity residential</button>
                                <img
                                    src={Network}
                                    alt=''
                                />
                            </Link>
                        </div>
                    </div>
                </div>
                <div className='row'>
                    <Link to='/products/2' className='link'>
                        <button className='button'>Switchboards and enclosures</button>
                        <img src={Enclosures} alt='' />
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default Categories
