import React from 'react'
import './header.css'
import logo from '../../assets/logos/logo.svg'
import { NavLink } from 'react-router-dom'
import SearchIcon from '@mui/icons-material/Search'
import PersonOutlineOutlinedIcon from '@mui/icons-material/PersonOutlineOutlined'
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined'
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown'
import LogoutOutlinedIcon from '@mui/icons-material/LogoutOutlined';
import LanguageImg from '../../assets/en.png'
import { useDispatch, useSelector } from 'react-redux'
import { useState } from 'react'
import Cart from '../cart/Cart'
import { loginOut } from '../../redux/userSlice'

const Header = () => {
    const handlerIconOnclick = () => {
        document.querySelector('.header__menu').classList.toggle('active')
        document.querySelector('.header__icon').classList.toggle('active')
        document.querySelector('body').classList.toggle('lock')
    }
    const products = useSelector((state) => state.cart.products)
    const [open, setOpen] = useState(false)
    const user = useSelector((state) => state.user.currentUser)
    const dispatch = useDispatch()

    const handleLogOut = () => {
        console.log('logout')
        dispatch(loginOut())
    }

    return (
        <header className='header'>
            <div className='header__row'>
                <nav className='header__menu menu'>
                    <div className='header__add'>
                        <div className='header__languages'>
                            <img src={LanguageImg} alt='' />
                            <KeyboardArrowDownIcon className='header__icons' />
                        </div>
                        <SearchIcon className='header__icons' />
                        {user ? (
                            <LogoutOutlinedIcon
                                className='header__icons'
                                onClick={() => handleLogOut()}
                            />
                        ) : (
                            <NavLink to='/login'>
                                <PersonOutlineOutlinedIcon className='header__icons' />
                            </NavLink>
                        )}

                        <div className='header__cartIcon'>
                            <ShoppingCartOutlinedIcon
                                className='header__icons'
                                onClick={() => setOpen(!open)}
                            />
                            <span>{products.length}</span>
                        </div>
                    </div>
                    <div className='menu__body'>
                        <ul className='menu__list'>
                            <li>
                                <NavLink end to='/' className='menu__link'>
                                    Home
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to='/about-us' className='menu__link'>
                                    About Us
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to='/our-projects' className='menu__link'>
                                    Our projects
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to='/our-products' className='menu__link'>
                                    Our products
                                </NavLink>
                            </li>
                            <li>
                                <NavLink to='/contact' className='menu__link'>
                                    Contact
                                </NavLink>
                            </li>
                        </ul>
                    </div>
                </nav>
                <div className='header__logo ibg'>
                    <a href={'/'} className='logo_link'>
                        <img src={logo} alt='' />
                    </a>
                </div>
                <div className='header__icon-wrapper'>
                    <div className='header__icon' onClick={handlerIconOnclick}>
                        <span></span>
                    </div>
                </div>
            </div>
            {open && <Cart />}
        </header>
    )
}

export default Header
