import { useEffect } from 'react'

import './woodhouses.css'
import jpg1 from '../../assets/shop/01.jpg'
import jpg2 from '../../assets/shop/02.jpg'
import jpg3 from '../../assets/shop/03.JPG'
import jpg4 from '../../assets/shop/04.JPG'
import jpg5 from '../../assets/shop/05.jpg'
import jpg6 from '../../assets/shop/06.JPG'
import jpg7 from '../../assets/shop/07.JPG'
import jpg8 from '../../assets/shop/08.JPG'
import logo from '../../assets/logos/logo.svg'

const Woodhouses = () => {
  useEffect(() => {
    let ibg = document.querySelectorAll('.ibg')
    for (var i = 0; i < ibg.length; i++) {
        if (ibg[i].querySelector('img')) {
            ibg[i].style.backgroundImage =
                'url(' + ibg[i].querySelector('img').getAttribute('src') + ')'
        }
    }
})
    return (
        <div className='shop'>
            <div className='shop__body'>
                <div className='shop__column shop__column_b'>
                    <div className='shop__block block-shop'>
                        <div className='block-shop__row'>
                            <div className='block-shop__column'>
                                <a href='' className='block-shop__item item'>
                                    <span className='item__logo'>
                                        <img src={logo} alt='' />
                                    </span>
                                    <span className='item__image ibg'>
                                        <img src={jpg2} alt='' />
                                    </span>
                                </a>
                            </div>
                            <div className='block-shop__column'>
                                <a href='' className='block-shop__item item'>
                                    <span className='item__logo'>
                                        <img src={logo} alt='' />
                                    </span>
                                    <span className='item__image ibg'>
                                        <img src={jpg1} alt='' />
                                    </span>
                                </a>
                            </div>
                        </div>
                        <div className='block-shop__row_bottom'>
                            <a href='' className='item'>
                                <span className='item__logo'>
                                    <img src={logo} alt='' />
                                </span>
                                <span className='item__image ibg'>
                                    <img src={jpg3} alt='' />
                                </span>
                            </a>
                        </div>
                    </div>
                </div>
                <div className='shop__column'>
                    <a href='' className='item'>
                        <div className='shop__item'></div>
                        <span className='item__logo'>
                            <img src={logo} alt='' />
                        </span>
                        <span className='item__image ibg'>
                            <img src={jpg6} alt='' />
                        </span>
                    </a>
                </div>
                <div className='shop__column'>
                    <a href='' className='item'>
                        <div className='shop__item'></div>
                        <span className='item__logo'>
                            <img src={logo} alt='' />
                        </span>
                        <span className='item__image ibg'>
                            <img src={jpg8} alt='' />
                        </span>
                    </a>
                </div>
                <div className='shop__column shop__column_b'>
                    <div className='shop__block block-shop'>
                        <div className='block-shop__row'>
                            <div className='block-shop__column'>
                                <a href='' className='block-shop__item item'>
                                    <span className='item__logo'>
                                        <img src={logo} alt='' />
                                    </span>
                                    <span className='item__image ibg'>
                                        <img src={jpg4} alt='' />
                                    </span>
                                </a>
                            </div>
                            <div className='block-shop__column'>
                                <a href='' className='block-shop__item item'>
                                    <span className='item__logo'>
                                        <img src={logo} alt='' />
                                    </span>
                                    <span className='item__image ibg'>
                                        <img src={jpg5} alt='' />
                                    </span>
                                </a>
                            </div>
                        </div>
                        <div className='block-shop__row_bottom'>
                            <a href='' className='item'>
                                <span className='item__logo'>
                                    <img src={logo} alt='' />
                                </span>
                                <span className='item__image ibg'>
                                    <img src={jpg7} alt='' />
                                </span>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <div className='shop__footer'>
                <div className='container'>
                    <a href='' className='shop__btn'>
                        Electricity in a wooden house
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Woodhouses
